import { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { BtnFilledGray } from '../../shared/Buttons/BaseBtn/BaseBtn';
import { signUp } from '../../../services/userService';
import { signIn } from '../../../services/userService';
import Input from '../../shared/Input';
import './SignUpForm.scss';


const SignUpForm = ({ countries }) => {
    const history = useHistory();
    const [formData, setFormData] = useState({
        email: null,
        password: null,
        firstName: null,
        lastName: null,
        year: null,
        month: null,
        day: null,
        country: null
    });

    const handleChange = (e) => {
        const newFormData = { ...formData };
        newFormData[e.target.name] = e.target.value;
        setFormData(newFormData);
    }

    const submitForm = (e) => {
        e.preventDefault();

        if (formData.email && formData.password && formData.firstName
            && formData.lastName && formData.year && formData.month
            && formData.day && formData.country) {
            signUp(formData)
                .then(res => res.json())
                .then(data => {
                    if (data.error) {
                        throw data.error;
                    }
                    signIn(formData.email, formData.password)
                        .then(res => {
                            history.push('/onboarding');
                        })
                        .catch(err => alert(err));
                })
                .catch(err => {
                    alert(err);
                });
        }

    }
    
    return (
        <form className='signup-form' onSubmit={submitForm}>
            <h1 className="signup-header">Create Account</h1>
            <Input id="email" type="email" placeholder="Enter your email address" onChange={handleChange} />
            <Input id="password" type="password" placeholder="Enter your password" onChange={handleChange} />
            <div className="signup-names-input">
                <Input id="firstName" type="text" placeholder="First name" onChange={handleChange} />
                <Input id="lastName" type="text" placeholder="Last name" onChange={handleChange} />
            </div>
            <div className="date-of-birth">
                <Input id="month" type="number" placeholder="Month" onChange={handleChange} />
                <Input id="day" type="number" placeholder="Day" onChange={handleChange} />
                <Input id="year" type="number" placeholder="Year" onChange={handleChange} />
            </div>
            <div className="country">
                <select name="country" id="country" onChange={handleChange}>
                    <option value="" label="Country/Region">Select a country ... </option>
                    {countries ? countries.map((country, index) => (
                        <option key={index} value={country.alpha2Code} label={country.name}>{country.name}</option>
                    )) : (<option value="Loading..." label="Loading...">Loading...</option>)}
                </select>
                <label htmlFor="country" className="input-text-label" data-content=""></label>
            </div>
            <BtnFilledGray className="sign-btn-gray" onClick={() => { }}>Sign up</BtnFilledGray>
            <span className="policy-agreement-txt">By clicking Create account, I agree that I have read and <span className="second-line">accepted the <a href="#">Terms of Use</a> and <a href="#">Privacy Policy</a>.</span></span>
        </form>
    )
}

export default SignUpForm;