import firebase from '../firebase';
const googleProvider = new firebase.auth.GoogleAuthProvider();
const facebookProvider = new firebase.auth.FacebookAuthProvider();

export const signUp = (data) => {
    return fetch('http://localhost:5000/user/signup', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
    });
}

export const createUsername = (username, idToken) => {
    return fetch('http://localhost:5000/user/createusername', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + idToken
        },
        body: JSON.stringify({ username }),
    });
}

export const signOut = () => {
    return firebase.auth().signOut();
}

export const signIn = (email, password) => {
    return firebase.auth().signInWithEmailAndPassword(email, password);
}

export const googleSignIn = (history) => {
    return firebase.auth().signInWithPopup(googleProvider)
        .then(res => {

            if (res.additionalUserInfo.isNewUser) {

                const email = res.additionalUserInfo.profile.email;
                const firstName = res.additionalUserInfo.profile.given_name;
                const lastName = res.additionalUserInfo.profile.family_name;

                res.user.getIdToken()
                    .then(idToken => {
                        createDbUser({ email, firstName, lastName }, idToken)
                            .then(res => res.json())
                            .then(data => {
                                if (data.error) {
                                    throw data.error;
                                }

                                history.push('/onboarding');
                            })
                            .catch(err => alert(err.message));

                    })
                    .catch(err => console.log(err));
            } else {
                history.push('/');
            }

        })
        .catch(err => alert(err));
}

export const facebookSignIn = (history) => {
    return firebase.auth().signInWithPopup(facebookProvider)
        .then(res => {

            if (res.additionalUserInfo.isNewUser) {

                const email = res.additionalUserInfo.profile.email;
                const firstName = res.additionalUserInfo.profile.first_name;
                const lastName = res.additionalUserInfo.profile.last_name;

                res.user.getIdToken()
                    .then(idToken => {
                        createDbUser({ email, firstName, lastName }, idToken)
                            .then(res => res.json())
                            .then(data => {
                                if (data.error) {
                                    throw data.error;
                                }

                                history.push('/onboarding');
                            })
                            .catch(err => alert(err.message));

                    })
                    .catch(err => console.log(err));
            } else {
                history.push('/');
            }
        })
        .catch(err => alert(err));
}

export const createDbUser = (data, idToken) => {
    return fetch('http://localhost:5000/user/createdbuser', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + idToken
        },
        body: JSON.stringify(data),
    });
}