import './Input.scss';

const Input = ( {id, type, min, max, required, placeholder, endText, value, onChange, onBlur} ) => {
    
    return (
        <div className="sign-input">
            <input 
                type={type}
                className="input-field"
                id={id}
                name={id}
                placeholder={placeholder}
                value={value}
                onBlur={onBlur}
                onChange={onChange}
                required={required}
                min={min}
                max={max}
            />

            <label
                htmlFor={id}
                className="input-label"
                data-content={placeholder}>
            </label>
            
            {endText ? <label className="input-end-label" data-content={endText}></label> : ''}
        </div>
    );
};

export default Input;
