import { useState } from 'react';

import Heading from './Heading';
import Input from './Input';
import Feature from './Feature';

import { ReactComponent as OneClick } from '../assets/one-click.svg';
import { ReactComponent as YourOwn } from '../assets/your-own.svg';
import { ReactComponent as DiscoverSvg } from '../assets/discover.svg';
import { ReactComponent as MakeReading } from '../assets/make-reading.svg';

import './ComingSoonMain.scss';

function ComingSoonMain() {
    const [subscribed, setSubscribed] = useState(false);

    const firstFeatureProps = {
        title: 'One-Click\nUnsubscribel',
        content:
            'Dont like what youre reading?\nRemove the feed and\nnever see it again.',
    };

    const secondFeatureProps = {
        title: 'Your Own\nLetterbox Email',
        content:
            'See something cool on the web?\nSubscribe with your new\ncustom email.',
    };

    const thirdFeatureProps = {
        title: 'Discover \nTop Newsletters',
        content:
            'Browse through your interests\nand find the hottest newsletters\nin that space.',
    };

    const fourthFeatureProps = {
        title: 'Make Reading\nPleasant Again',
        content:
            'Group newsletters into folders,\nquickly mark as read, switch\nbetween day/night mode.',
    };

    return (
        <div className="main">
            <div className="main-element">
                <div className="heading-container">
                    <Heading subscribed={subscribed} />
                </div>

                <div className="input-container">
                    <Input
                        subscribed={subscribed}
                        setSubscribed={setSubscribed}
                    />
                </div>

                <div className="paragraphs">
                    <div className="paragraph-container">
                        <Feature
                            props={firstFeatureProps}
                            icon={<OneClick />}
                        />
                        <Feature
                            props={secondFeatureProps}
                            icon={<YourOwn />}
                        />
                        <Feature
                            props={thirdFeatureProps}
                            icon={<DiscoverSvg />}
                        />
                        <Feature
                            props={fourthFeatureProps}
                            icon={<MakeReading />}
                        />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ComingSoonMain;
