import { useState, useContext } from 'react';
import { useHistory } from 'react-router-dom';
import Btn from '../../shared/Buttons/BaseBtn';
import Input from '../../shared/Input';
import { AuthContext } from '../../../contexts/AuthContext';
import { createUsername } from '../../../services/userService';

import './GenerateMailForm.scss';

function GenerateMailForm() {
    const history = useHistory();
    const user = useContext(AuthContext);
    const [username, setUsername] = useState(null);

    const submitForm = async (e) => {
        e.preventDefault();

        if (username && user) {
            const idToken = await user.getIdToken(true);

            createUsername(username, idToken)
                .then(res => res.json())
                .then(data => {
                    if (data.error) {
                        throw data.error;
                    }

                    history.push('/');
                })
                .catch(err => alert(err));
        }
    }

    return (
        <form className="generate-mail-form" onSubmit={submitForm}>
            <div className="header-container">
                <h1>Letterbox mail</h1>

                <h4>Subscribtions for newsletters will be made with this mail.</h4>
            </div>

            <div className="input-container">
                <Input placeholder="Enter your username" endText="@letterbox.io"
                    onChange={(e) => setUsername(e.target.value)} />

                <Input placeholder="Enter your password" />
            </div>

            <div className="btn-container">
                <Btn className="btnClass">
                    Create Letterbox Mail
                </Btn>
            </div>
        </form>
    );
}

export default GenerateMailForm;
