import './InputText.scss';

const InputText = ({type="text", label, error, errorMessage, id, value, onBlur, onChange}) => {
    return (
        <div className="input-text">         
                <input className={error ? 'invalid-input' : ''} className="input-text-field" type={type} id={id} name= {id} value={value} placeholder={label} onBlur={onBlur} onChange={onChange} />
                {error ? (<div className="error">{errorMessage}</div>):null}
                <label htmlFor={id} className="input-text-label">{label}</label>
        </div>
    ) 
}

export default InputText;